<template>
  <div v-html="formattedText"></div>
</template>

<script>
export default {
  name: "table-a",
  data() {
    return {
      text: "<p>这是一个示例文本，其中包含网址：www.baidu.com 和 http://example.com 以及 https://example2.com</p>",
      text1: "<p>这是一个示例文本，其中包含网址：www.baidu.com 和 http://example.com 以及 https://example2.com</p>",
    };
  },
  computed: {
    formattedText() {
      // 更新后的正则表达式，去掉多余的转义字符
      const urlPattern =
        /((https?:\/\/)?([\da-z.-]+)\.([a-z.]{2,6})([\/\w .-]*)*\/?)/gi; // eslint-disable-line
      return this.text.replace(urlPattern, function (url) {
        // Check if the URL already starts with http or https, if not, add it
        let hyperlink = url;
        if (!hyperlink.startsWith("http")) {
          hyperlink = "http://" + hyperlink;
        }
        return `<a href="${hyperlink}" target="_blank">${url}</a>`;
      });
    },
  },
};
</script>

